<template>
    <div class="backgroundImg">
        <div class="setMain" v-if="Wsetpage=='main'">
            <ul>
                <li v-for="file in files" :key="file" v-on:click="changePageWords">
                    <span>{{ file.name }}</span>
                </li>
            </ul>
        </div>
        <div class="setWords" v-if="Wsetpage=='words'">
            <p>단어 리스트</p>
            <ul>
                <li v-for="word in firstJSON" :key="word">
                    <span>{{ word.word }}</span>
                </li>
            </ul>
            <p>뜻 리스트</p>
            <ul>
                <li v-for="word in firstJSON" :key="word">
                    <span>{{ word.meaning }}</span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import fileList from '@/assets/words/files.json';
import firJSON from '@/assets/words/firstWords.json';

const files = fileList;
const firstJSON = firJSON

export default{
    name: 'SetPage',
    components: {

    },
    data(){
        return{
            files,
            Wsetpage: 'main',
            firstJSON,
        };
    },
    methods:{
        changePageWords: function(){
            if(this.Wsetpage == 'main'){
                this.Wsetpage = 'words';
            } else if(this.Wsetpage == 'words'){
                this.Wsetpage = 'main';
            }
        }
    },
}
</script>
<style>
.backgroundImg{
    display:inline-block;
    position:relative;
    width:100vw;
    height:100vh;
    background-image: linear-gradient(180.00deg,rgb(206,255,185) 1.591%,rgba(255,255,255,0.2) 104.231%);
}
</style>